.header{
  // position: fixed;
  // top: 0;
  // left: 0;
  // right: 0;
  padding: 20px 0;

  margin-bottom: 250px;

  background: rgb(70, 70, 187);

  .container{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  &__menu{
    &-list{
      display: flex;
      align-items: center;
    }

    &-item{
      margin-right: 45px;

      &:last-child{
        margin-right: 0;
      }
    }
  }
}