.impact{
    position: relative;

    &__content{
        background: $purple;
        padding: 60px 0;
        text-align: center;
    }

    &--title{
        font-size: 30px;
        color: $white;
    }

    &_list{
        display: flex;
        align-items: flex-start;
        justify-content: space-between;
        margin: 50px -15px;

        @media(max-width: $maxDesc){
            flex-wrap: wrap;
            margin: 25px -15px;
        }
        
        @media(max-width: $maxMobile){            
            align-items: center;
            flex-direction: column;
        }

    }

    &_item{
        padding: 0 15px;
        flex-basis: 25%;

        @media(max-width: $maxDesc){
            padding: 25px 15px;
            flex-basis: 50%;
        }

        &--number{
            color: $green;
            font-size: 60px;
            line-height: 72px;
            letter-spacing: -1px;
            font-weight: bold;

            @media(max-width: $maxDesc){
                font-size: 48px;
                line-height: 58px;
            }
        }

        &--desc{
            color: $white;
            font-style: normal;
            font-weight: 600;
            font-size: 18px;
            line-height: 26px;
            text-transform: uppercase;
            max-width: 300px;
            margin: 0 auto;
        }
    }

    &--text{
        font-size: 16px;
        line-height: 24px;
        color: $white;
        max-width: 480px;
        margin: 0 auto;
    }

    &--buttons{
        display: flex;
        align-items: center;
        max-width: 500px;
        margin: 30px auto 0 auto;

        @media(max-width: $maxMobile){            
            flex-direction: column;
        }
    }

    &-btn{
        width: 100%;
        margin: 0 5px;
        text-transform: uppercase;
        font-weight: bold;
        font-size: 16px;
        line-height: 19px;
        padding: 13px 10px;
        color: $white;
        background: $green;
        transition: background .3s ease, color .3s ease;

        @media(max-width: $maxMobile){            
            margin: 5px 0;
            flex-basis: 100%;
        }

        &-white{
            color: $green;
            background: $white;
            transition: background .3s ease, color .3s ease;
        }

        @media(min-width: $maxDesc){            
            &:hover{
                background: $greenHover;
                color: $white;
                transition: background .3s ease, color .3s ease;
            }
        }
    }

    &--img{
        max-width: 300px;
        max-height: 1170px;
        position: absolute;
        left: 0;
        top: 0;
        bottom: 50%;
        margin: auto;

        @media(max-width: $maxMobile){            
            max-height: 450px;
            max-width: 150px;
            bottom: 0;
        }
    }
}