.email_contact{
    background: $blue;
    padding: 35px 0;
    overflow: hidden;

    &-title{
        font-weight: bold;
        font-size: 24px;
        line-height: 26px;
        color: $white;
    }

    &-text{
        @media(min-width: $maxTablet) {
            min-width: 50%;
        }
    }

    &-desc{
        font-weight: normal;
        font-size: 18px;
        line-height: 20px;
        color: $white;
        margin-top: 5px;
    }

    &-form{
        @media(min-width: $maxTablet) {
            margin-left: 25px;
            min-width: 45%;
        }

        @media(max-width: $maxTablet) {
            margin-top: 15px;
        }

        .wpcf7-form-control-wrap{
            width: 100%;

            @media(min-width: $maxTablet) {
                flex-basis: 70%;
            }
        }

        .wpcf7-form{
            display: flex;
            align-items: center;
            justify-content: center;
            position: relative;

            &.failed,
            &.invalid{
                flex-wrap: wrap;
            }

            @media(max-width: $maxDesc) {
                flex-direction: column;
            }
        }

        .wpcf7 .ajax-loader{
            position: absolute;
            right: -40px;
            top: 0;
            bottom: 0;
            margin: auto;

            @media(max-width: $maxTablet) {   
                bottom: 0;
                left: 0;
                right: 0;
            }
        }

        input[type="email"]{
            background: $white;
            font-size: 18px;
            line-height: 30px;
            color: $black;
            padding: 8px 10px;
            width: 100%;
        }

        input[type="submit"]{
            background: $purple;
            color: $white;
            font-weight: bold;
            font-size: 16px;
            line-height: 19px;
            text-align: center;
            letter-spacing: 1px;
            text-transform: uppercase;
            padding: 13.5px 10px;
            cursor: pointer;
            transition: background .3s ease;

            @media(max-width: $maxDesc) {
                width: 100%;
                margin-top: 5px;
            }

            &:hover{
                background: $purpleHover;
                transition: background .3s ease;
            }
        }
    }

    .container{
        display: flex;
        align-items: center;
        justify-content: space-between;

        @media(max-width: $maxTablet) {
            flex-direction: column;
            text-align: center;
        }
    }
}